
#toolbar {
    position: fixed;
    height: 5em;
    top: 0;
    left: 0;
    right: 0;
    padding: 0.3em;
    border-bottom: black solid;

    background-image: url(data:image/png;base64,);
    background-size: 10em;
    image-rendering: -moz-crisp-edges;
    image-rendering: pixelated;
}

#title {
    font-size: 3em;
    font-weight: bold;
    background: url(data:image/png;base64,) 0.52em 0.52em;
    background-size: 3.35em;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;

}

#title img {
    height: 0.8em;
}

#main {
    padding-top: 6em;
}
#npcPos {
    position: relative;
}

#npcPos:after {
    background: black none repeat scroll 0 0;
    position: absolute;
    bottom: 0;
    left: 0;

    content: "";
    width: 0;

    height: 1px;
    margin: 5px 0 0;
    opacity: 0;
    transition: all 0.2s ease-in-out;
}

#npcPos:hover:after {
    width: 100%;
    opacity: 1;
}

#npcPos:hover {
    cursor: pointer;

}

#npcPos img {
    height: 0.6em;
}
